.App {
  text-align: center;
  font-family: Arial, sans-serif;
  margin-top: 20px;
}

.App-header {
  background-color: #f1f1f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: 16px;
  color: black;
}

input[type="text"] {
  padding: 8px;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
}

button {
  background-color: #0056b3;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #004494;
}

.multi-select {
  width: 300px;
  margin-top: 10px;
  display: flex;
  justify-content: center;
}

select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.filtered-response {
  margin-top: 20px;
  padding: 10px;
  width: 300px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-align: left;
}

.filtered-response p {
  margin: 5px 0;
  font-size: 14px;
}

.label {
  font-weight: bold;
  margin-right: 5px;
}

